.MenuBarContainer{
    position: fixed;
    left: 0;
    top: 0;
    width: 300px;
    height: 100%;
    background-color: #040b14;
    display: flex;
    flex-direction: column;
}

.Profile{
    display: block;
    height: 270;
    width: 100%;
}

.ProfilePic{
    border: 8px solid #2c2f3f;
    border-radius: 50%;
    aspect-ratio: 1;
    width: 120px;
    margin: 15;
}

.TextName{
    font-weight: 600;
}
a{
    text-decoration: none;
    color: white
}

.SocialMediaContainer{
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}